import React, { Component } from "react";

import Info from "../images/info.png";

class Header extends Component {
  state = {};

  getLoadingMessage = () => {
    if (this.props.loadingState === "data") {
      return "Retrieving Data for ";
    } else if (this.props.loadingState === "train") {
      return "Training Neural Network for ";
    } else {
      return "Making Predictions for ";
    }
  };

  render() {
    return (
      <div id="header-div" style={{ backgroundColor: "#17223b" }}>
        <a
          href="https://github.com/nbalepur/Pytorch-Stock-RNN"
          target="blank"
          rel="noreferrer"
        >
          <img
            class="grow"
            style={{
              float: "right",
              marginTop: 15,
              marginRight: 25,
              width: 30,
              height: 30,
            }}
            src={Info}
            alt="info"
          ></img>
        </a>
        <div class="container">
          <br></br>
          <br></br>
          <br></br>
          <div class="row">
            <div class="col-md-2"></div>
            <div class="col-md-8" align="center">
              <h1 style={{ color: "white", fontSize: 50 }}>
                RNN Stock Predictor
              </h1>
              <h4 style={{ color: "white" }}>
                Select a stock below to get started!
              </h4>
              <br />
              <div class="btn-group" role="group" aria-label="Basic example">
                <button
                  type="button"
                  class="btn btn-primary"
                  onClick={() => this.props.handleButtonStockData("AAPL")}
                >
                  AAPL
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  onClick={() => this.props.handleButtonStockData("AMZN")}
                >
                  AMZN
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  onClick={() => this.props.handleButtonStockData("FB")}
                >
                  FB
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  onClick={() => this.props.handleButtonStockData("GOOGL")}
                >
                  GOOGL
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  onClick={() => this.props.handleButtonStockData("TSLA")}
                >
                  TSLA
                </button>
              </div>
              <br />
              <br />
              <h4 style={{ color: "white" }}>
                <i>
                  Note: To train with custom stocks, follow the instructions{" "}
                  <a
                    href="https://github.com/nbalepur/Pytorch-Stock-RNN"
                    class="stock-link"
                    target="blank"
                    rel="norefferer"
                  >
                    here
                  </a>
                </i>
              </h4>
              <div class="col-md-10"></div>
            </div>
            <div class="col-md-2"></div>
          </div>
          <br></br>
          <br></br>
          {this.props.loading && (
            <div class="col-md-12" align="center">
              <h2 class="sr-only" style={{ color: "white" }}>
                {"Training Neural Network for "}
                <a
                  class="stock-link"
                  href={
                    "https://www.marketwatch.com/investing/fund/" +
                    this.props.stock.toLowerCase()
                  }
                  target="blank"
                  rel="noreferrer"
                >
                  {this.props.stock.toUpperCase()}
                </a>{" "}
              </h2>{" "}
              <br />
              <div class="spinner-border" />
              <br></br>
              <br></br>
            </div>
          )}
          {!this.props.loading && this.props.isValid && (
            <div class="col-md-12" align="center">
              <h2 style={{ color: "white" }}>
                Showing Results for{" "}
                <span>
                  <a
                    class="stock-link"
                    href={
                      "https://www.marketwatch.com/investing/fund/" +
                      this.props.stock.toLowerCase()
                    }
                    target="blank"
                    rel="noreferrer"
                  >
                    {this.props.stock.toUpperCase()}
                  </a>
                </span>{" "}
              </h2>{" "}
              <br></br>
              <br></br>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Header;
